<template>
  <div id="caic-common-picker">
    <van-field v-model="value"
               :label="label"
               is-link
               :readOnly="true"
               :border="border"
               :label-align="labelAlign"
               :input-align="inputAlign"
               :placeholder="tipMsg"
               @click="selectType">
      <div slot="right-icon"
           @click="selectType">
        <slot name="right-icon">
        </slot>
      </div>
    </van-field>
    <van-popup v-model="selTypePicker"
               position="bottom">
      <van-picker :columns='columns'
                  show-toolbar
                  :title="pickerTitle"
                  :item-height="itemHeight"
                  :confirm-button-text="confirmButtonText"
                  :cancel-button-text="cancelButtonText"
                  :visible-item-count="visibleItemCount"
                  @cancel='selTypeCancel'
                  @confirm='selTypeSure'
                  @change="selTypeChange">
      </van-picker>
    </van-popup>
  </div>
</template>
<script>
/**
 * ===============done=======================
 * 
 * v-model 
 * label(String)
 * border Boolean   true
 * labelAlign String left center right  left
 * inputAlign String left center right  left
 * placeholder String 
 * title String picker
 * itemHeight Number  44
 * confirmButtonText String  
 * cancelButtonText String  
 * visibleItemCount Number  5
 * columns(Array)
 * 
 * @confirm() (Function) 
 * @cancel() Function 
 * @change() Function 
 * 
 * right-icon 
 * ==============doing=======================
 * 
 * 
 * ==============hope=========================
 * 
 */
import Vue from 'vue'
import {
  Picker,
  Popup,
  Field,
} from 'vant'
Vue.use(Picker).use(Popup).use(Field)
export default {
  name: 'caic-common-picker',
  props: {
    value: {
      type: [String, Number],
      required: true
    },
    label: {
      type: String,
      required: true
    },
    border: {
      type: Boolean,
      default: true
    },
    labelAlign: {
      type: String,
      default: 'left'
    },
    inputAlign: {
      type: String,
      default: 'left'
    },

    placeholder: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    itemHeight: {
      type: Number,
      default: 44
    },
    confirmButtonText: {
      type: String,
      default: ''
    },
    cancelButtonText: {
      type: String,
      default: ''
    },
    visibleItemCount: {
      type: Number,
      default: 5
    },
    columns: {
      type: Array,
      required: true
    }

  },
  data () {
    return {
      selTypePicker: false,
      tipMsg: null
    }
  },
  created () {
    this.tipMsg = this.placeholder
  },
  methods: {
    selectType: function () {
      this.selTypePicker = true
    },
    selTypeCancel: function (value, index) {
      this.selTypePicker = false
      this.$emit('cancel', value, index)
    },
    selTypeSure: function (value, index) {
      this.selTypePicker = false
      this.$emit('confirm', value, index)
    },
    selTypeChange: function (picker, index) {
      this.$emit('change', picker, index)
    }
  }
}
</script>
<style lang="stylus" scoped>
@import '../../css/caic.styl'

#caic-common-picker
  .van-cell.van-cell--clickable.van-field
    changeFontSize(14px)
</style>



